<template>
  <div class="main_navs">
    <Header></Header>
    <div class="container">
      <div class="h1_calc">
        {{$t('calc_title')}}
      </div>
      <div class="h1_calc_desc">
        {{$t('casb_desc')}}
      </div>
      <div class="p_subjects">
        <label  v-for="(item,index) in $store.state.subjects" :key="index">
          <input class="d-none" @input="getWork(item,index)" type="checkbox" v-model="dynamicItems[index].active">
          <div class="sub_cat" :class="{'active':dynamicItems[index].active}">
            {{item.title}}
            <svg v-if="!isSubject" width="20" height="21" viewBox="0 0 20 21" fill="none" xmlns="http://www.w3.org/2000/svg">
              <circle cx="10" cy="10.5" r="10" fill="#D9D9D9"/>
              <path d="M15.373 6.74805L7.8125 14.3086L4.62695 11.123L3.99805 11.752L7.49805 15.252L7.8125 15.5527L8.12695 15.252L16.002 7.37695L15.373 6.74805Z" fill="#043936"/>
            </svg>

            <svg v-else width="20" height="21" viewBox="0 0 20 21" fill="none" xmlns="http://www.w3.org/2000/svg">
              <circle cx="10" cy="10.5" r="10" fill="#043936"/>
              <path d="M6.1582 6.5293L5.5293 7.1582L9.37109 11L5.5293 14.8418L6.1582 15.4707L10 11.6289L13.8418 15.4707L14.4707 14.8418L10.6289 11L14.4707 7.1582L13.8418 6.5293L10 10.3711L6.1582 6.5293Z" fill="white"/>
            </svg>

          </div>
        </label>
      </div>
      <div class="row">
        <div  class="row" v-if="$store.state.isLoading">
          <div class="col-xl-6 mb-4"  v-for="itemm in 10" :key="itemm">
            <b-skeleton-img ></b-skeleton-img>
          </div>
        </div>
        <div class="col-xl-6" v-for="(item,inde) in $store.state.professions_web.results" :key="inde">
          <div class="prof_card calc_card">
            <div class="col-xl-2">
              <img class="cover_img" :src="item.cover_image_url" alt="">
            </div>
            <div class="col-xl-9">
              <div class="prof_title mb-3">{{item.title}}</div>
              <div class="prof_desc " v-for="(sb,ind) in item.subjects " :key="ind">
                {{sb.title}}
              </div>
              <div class="prof_fa">
                <div class="prof_price">{{$t('salery')}}: {{item.max_salary}}</div>
                <div class="prof_link">
                  <router-link class="decoration" :to="`/professions/${item.id}`">{{$t('more')}}</router-link>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <Footer></Footer>
  </div>
</template>
<script>
import Header from "../components/Header";
import Footer from "../components/Footer";
export default {
  components: {Footer, Header},
  data(){
    return{
      cres:[],
      subId:[],
      isSubject:false,
      isActive:[],
      Sub:[],
      dynamicItems: [
      ],
      selectedAll:false,
    }
  },
  mounted() {
    this.$store.dispatch('getSubjects')
    this.$store.dispatch('getSubjectWork')
    for (var i=0;i<30;i++){
      this.dynamicItems.push({
        active:false
      })
    }
  },
  methods:{

    getWork(item ){
      var k=''
      var index = this.Sub.indexOf(item);

      if (index === -1) {
        this.Sub.push(item);
      } else {
        this.Sub.splice(index, 1);
      }

      this.Sub.forEach(el=>{
        k=k+`subjects=${el.id}&`
      })

      this.$store.dispatch('getSubjectWork',k)
      console.log(this.Sub,"dsa")
    },


  }

}
</script>
